export class HomePage extends Component {
  constructor(props) {
    super(props);
    // console.log('props are', props);
    this.audioBlob = null;
    this.state = {
      showingInfoWindow: false,
      activeMarker: {},
      selectedPlace: {},
      newMarkerInProgress: null,
      markers: [
        {
          name: "Current position",
          position: {
            lat: 37.77,
            lng: -122.42
          }
        },
        {
          name: "Current position",
          position: {
            lat: 55.77,
            lng: -122.42
          }
        }
      ]
    };
  }

  componentWillMount = () => {
    // console.log('comp will mount.');

  }

  stopRecording = () => {
    this.mediaRecorder.stop();
  };

  startRecording = () => {
    // console.log('start recording..', window.navigator);
    window.navigator.mediaDevices.getUserMedia({ audio: true })
      .then(stream => {
        // console.log('hello media recordder...');
        const mediaRecorder = new MediaRecorder(stream);
        this.mediaRecorder = mediaRecorder;
        this.mediaRecorder.start();

        this.audioChunks = [];
        this.mediaRecorder.addEventListener("dataavailable", event => {
          // console.log('recording.....');
          this.audioChunks.push(event.data);
        });

        mediaRecorder.addEventListener("stop", () => {
          const audioBlob = new Blob(this.audioChunks);
          this.audioBlob = audioBlob;
          const audioUrl = URL.createObjectURL(audioBlob);
          // console.log('audio blob is..', audioBlob);
          const audio = new Audio(audioUrl);
          this.audio = audio;
          this.audio.play();

          this.setState({
            newMarkerInProgress: {
              ...this.state.newMarkerInProgress,
              audioBlob: this.audioBlob
            }
          });
          this.audioBlob = null;
          this.audio = null;
          this.audioChunks = null;
        });

        // setTimeout(() => {
        //   this.mediaRecorder.stop();
        // }, 3000);
      });
  };

  onMarkerClick = (props, marker, e) => {
    this.setState({
      selectedPlace: props,
      activeMarker: marker,
      showingInfoWindow: true
    });
  };

  clickMarker = (marker) => {
    const audioUrl = URL.createObjectURL(marker.audioBlob);
    const audio = new Audio(audioUrl);
    audio.play();
  };

  onClose = props => {
    if (this.state.showingInfoWindow) {
      this.setState({
        showingInfoWindow: false,
        activeMarker: null
      });
    }
  };


  getMyLocation() {
    const location = window.navigator && window.navigator.geolocation;
    
    if (location) {
      // console.log('l;ocation defined');

      location.getCurrentPosition((position) => {
        // console.log('position');
        this.setState({
          latitude: position.coords.latitude,
          longitude: position.coords.longitude,
        })
      }, (error) => {
        console.log('error location', error);
        // this.setState({ latitude: 'err-latitude', longitude: 'err-longitude' })
      })
    }
  }

  createPinAtCenter = () => {
    console.log('get map coordinates...', this.props);
  }
  // getMyLocation = (e) => {
  //   let location = null;
  //   let latitude = null;
  //   let longitude = null;
  //   if (window.navigator && window.navigator.geolocation) {
  //       location = window.navigator.geolocation;
  //   }
  //   if (location){
  //       location.getCurrentPosition(function (position) {
  //           latitude = position.coords.latitude;
  //           longitude= position.coords.longitude;
  //           console.log('latitude', latitude);
  //           console.log('longitude', longitude);
  //       })
  //   }
  //   this.setState({latitude: latitude, longitude: longitude})
  // }

  mapClicked = (mapProps, map, event) => {
    console.log('map clicked..', event);
    const lat = event.latLng.lat();
    const lng = event.latLng.lng();
    const { markers } = this.state;


    this.setState({
      markers,
      newMarkerInProgress: {
        position: {
          lat,
          lng
        }
      }
    });
  };

  saveNewPin = () => {
    let {
      newMarkerInProgress,
      markers
    } = this.state;

    markers.push({
      name: "Current position",
      position: {
        lat: newMarkerInProgress.position.lat,
        lng: newMarkerInProgress.position.lng
      },
      audioBlob: newMarkerInProgress.audioBlob
    });

    this.setState({
      markers,
      newMarkerInProgress: null
    });
  };

  cancelNewPin = () => {
    this.setState({
      newMarkerInProgress: null
    });
  };

  render() {
    const { newMarkerInProgress } = this.state;
    console.log('newMarkerInProgress', newMarkerInProgress);

    return (
      <div style={{ width: '650px', height: '650px' }}>
      <button onClick={this.startRecording}>record</button>
      
      <button onClick={this.getMyLocation}>get my location</button>
      <button onClick={this.createPinAtCenter}>create pin at center of map</button>
{ newMarkerInProgress ? (
          <div>
            Add new ..
            <input type="text" />
            <button onClick={this.saveNewPin}>Save</button>
            <button onClick={this.cancelNewPin}>Cancel</button>
            <button onClick={this.startRecording}>record</button>
            <button onClick={this.stopRecording}>stop</button>
          </div>
        ) : null }
        <div style={detailedMarkerInfo}>
          <p>hello detailed marker info</p>
        </div>

        <button onClick={() => { console.log('start now'); recognition.start(); }}>start recognition</button>
        <button onClick={() => { console.log('end now'); recognition.stop(); }}>stop recognition</button>


        <Map
          google={this.props.google}
          zoom={14}
          style={mapStyles}
          initialCenter={{
           lat: 26.35869,
           lng: -80.0831
          }}
          streetViewControl={false}
          mapTypeControl={false}
          onClick={this.mapClicked}
        >
          {this.state.markers.map((marker, index) => (
            <Marker
              key={index}
              position={marker.position}
              onClick={() => { this.clickMarker(marker); }}
              draggable={true}
              onDragend={(t, map, coord) => this.onMarkerDragEnd(coord, index)}
              name={marker.name}
            />
          ))}
          <Marker
            onClick={() => { this.onMarkerClick(marker); }}
            name={'Kenyatta International Convention Centre'}
          />
          <InfoWindow
            marker={this.state.activeMarker}
            visible={this.state.showingInfoWindow}
            onClose={this.onClose}
          >
            <div>
              <h4>{this.state.selectedPlace.name}</h4>
            </div>
          </InfoWindow>
        </Map>
        

      </div>
    );
  }
}

